<template>
  <div class="container">
    <!--  -->
    <div class="nav-cate">
      <div class="navigation">
        <Navigation @GetSelNav="GetSelNav"/>
      </div>
      <div class="category">
        <Category @GetSelCate="GetSelCate"/>
      </div>
    </div>
    <!--  -->
    <div class="title">
      <div class="title-input">
        <el-input
          placeholder=""
          v-model="titleInput"
          clearable>
        </el-input>
      </div>
      <div class="publish-but">
        <el-button @click="publish" type="primary" plain>{{publishButContent}}</el-button>
      </div>
    </div>
    <!--  -->
    <div class="editor">
      <MarkdownEditor ref="markdownEditor" v-model="markdownContent" height="700px" />
    </div>

  </div>
</template>

<script>
import Navigation from '@/views/admin/editor/Navigation.vue'
import Category from '@/views/admin/editor/Category.vue'
import MarkdownEditor from '@/components/admin/MarkdownEditor/Index.vue'


import { PostArticle } from '@/api/api'
import { UpdateArticle } from '@/api/api'
import util from '../../../utils/utils'

var article
var type

export default {
  components: {Navigation, Category, MarkdownEditor},
  data() {
    return {
      navName: '',
      navTag: '',
      categoryName: '',
      categoryTag: '',
      titleInput: '',
      markdownContent: '',
      publishButContent: '',
      aritcleId: '',
      languageTypeList: {
        'en': 'en_US',
        'zh': 'zh_CN',
        'es': 'es_ES'
      }
    }
  },
  computed: {
    language() {
      return this.languageTypeList['zh']
    }
  },
  created() {
    // 
    article = this.$route.query.article
    type = this.$route.query.type
    if(type == 'editor') {
      this.aritcleId = article.id
      this.titleInput = article.title
      this.markdownContent = article.detail
      this.navTag = article.navigation_tag
      this.navName = article.navigation_name
      this.categoryTag = article.category_tag
      this.categoryName = article.category_name
      this.publishButContent = ''
    } 
  },
  mounted() {
    // Editor.vue, , Menu.vue
    util.$on('activatePublish',()=>{
      this.publishButContent = ''
      this.titleInput = ''
      this.markdownContent = ''
    })
    // 
    if(type == 'editor') {
      util.$emit('setNavInputValue', article)
      util.$emit('setCateInputValue', article)
    }
  },
  methods: {
    publish() {
      //this.html = this.$refs.markdownEditor.getHtml()
      var title       = this.titleInput
      var detail      = this.$refs.markdownEditor.getValue()
      var navTag       = this.navTag
      var navName      = this.navName
      var categoryTag  = this.categoryTag
      var categoryName = this.categoryName
      
      console.log('navName', navName)
      console.log('navTag', navTag)
      console.log('categoryName', categoryName)
      console.log('categoryTag', categoryTag)
      console.log('title', title)
      console.log('detail', detail)

      if (title == '' ) {
        this.errMsg('','')
        return
      }
      if (detail == '' ) {
        this.errMsg('','')
        return
      }
      if (navTag == '' ) {
        this.errMsg('','')
        return
      }
      if (categoryTag == '' ) {
        this.errMsg('','')
        return
      }

      if (this.publishButContent == '') {
        this.updateArticle(this.aritcleId, title, detail, navTag, navName, categoryTag, categoryName)
        return
      }

      PostArticle(title, detail, navTag, navName, categoryTag, categoryName).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          // location.reload()
          this.successMsg('')
          this.titleInput = ''
          this.markdownContent = ''
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    updateArticle(id, title, detail, navTag, navName, categoryTag, categoryName) {
      UpdateArticle(id, title, detail, navTag, navName, categoryTag, categoryName).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.successMsg('')
          util.$emit('articleMgtTabActive')
          this.$router.push({path:'/admin/article_mgt'})
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // tag
    GetSelCate(item) {
      this.categoryName = item.name
      this.categoryTag = item.tag
    },
    // tag
    GetSelNav(item) {
      this.navName = item.name
      this.navTag = item.tag
    },
    // 
    successMsg(msg) {
      this.$message({
        type: 'success',
        message: msg
      });
    },
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
  }
}
</script>

<style scoped>
.nav-cate {
  display: flex;
  width: 700px
}
.navigation,.category {
  width: 50%
}
.title {
  display: flex;
  margin-bottom: 20px;
  width: 100%
}
.title-input {
  width: 50%
}
.publish-but {
  display: flex;
  align-items: center;
  margin-left: 5px
}

.editor {
  width: 100%;
  height: 100%;
}
</style>
