<template>
  <div class="container">
    <div style="">
      <!--  -->
      <div class="category">
        <div>
          <el-select v-model="cateInputValue" @change="GetSelCate" placeholder="">
            <el-option
              v-for="item in tableDataSelect"
              :key="item.tag"
              :label="item.name"
              :value="item"
              clearable="true">
            </el-option>
          </el-select>
        </div>
        <div class="cate-edit-but">
          <el-row>
            <el-button @click="cateEditBut" type="primary" plain>{{cateEditButContent}}</el-button>
          </el-row>
        </div>
      </div>
      <!--  -->
      <div v-if="cateEditIsOpen" class="cate-edit-page">
        <!--  -->
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          style="width: 100%"
          ref="singleTable">
          <el-table-column
            label="Name"
            prop="name">
          </el-table-column>
          <el-table-column
            align="right">  
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
              <el-popconfirm 
              title="" @onConfirm="delCate">
                <el-button style="margin-left: 10px;" @click="handleDelete(scope.$index, scope.row)" size="mini" type="danger" slot="reference">Delete</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
          <div class="get-nav-input">
            <el-select v-model="navInputValue" placeholder="">
              <el-option
                v-for="item in navOptions"
                :key="item.tag"
                :label="item.name"
                :value="item.tag"
                size="mini">
              </el-option>
            </el-select>
          </div>
        <div class="create-category">
          <div class="create-input">
            <el-input
              placeholder=""
              v-model="crateCateInput"
              size="small"
              clearable>
            </el-input>
          </div>
          <div class="create-but">
            <el-button @click="postCategory" size="small" type="primary" plain></el-button>
            <!-- <el-button type="text" @click="open"></el-button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { PostCategory } from '@/api/api'
import { DelCategory } from '@/api/api'
import { GetCategory } from '@/api/api'
import { GetNavigation } from '@/api/api'
import util from '../../../utils/utils'

export default {
  data() {
    return {
      navOptions: [],
      navInputValue:'', // 
      cateInputValue: '', // 
      cateEditButContent: '',
      crateCateInput: '',
      delCateContent: '', // 
      cateEditIsOpen: false,
      tableData: [], // 
      tableDataSelect: [], // 
      search: '', // 
    }
  },
  created() {
    // , Editor.vue
    util.$on('setCateInputValue', (item) => {
      this.cateInputValue = item.category_name
    })
    // tag, Navigation.vue
    util.$on('getCateByNav', (item) => {
      this.getCategoryByNav(item)
    })
    // , Navigation.vue
    util.$on('clearCateByNav', () => {
      this.cateInputValue = ''
    })
    
    //  
    this.getCategory()

  },
  methods: {
    // , 
    handleEdit(index, row) {
      console.log(index, row);
      //this.open2(row.name);
    },
    // , 
    handleDelete(index, row) {
      this.delCateContent = row
      //console.log(index, row);
    },
    delCate(){
      DelCategory(this.delCateContent.id).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.successMsg()
          this.getCategory()
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    cateEditBut() {
      this.getNavigation() // 

      if (this.cateEditIsOpen == true) {
        this.cateEditButContent = ''
        this.cateEditIsOpen = false
        return
      }
      if (this.cateEditIsOpen == false) {
        this.cateEditButContent = ''
        this.cateEditIsOpen = true
        return
      }
    },
    // 
    getNavigation() {
      GetNavigation().then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.navigation == null) {
            this.navOptions = []
            return
          }
          this.navOptions = response.data.data.navigation
          return
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    open2(inputValue) {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputValue: inputValue
      }).then(({ value }) => {
        if ( value == '' ) { this.errMsg('','') } 
        else { console.log(value) }
      }).catch(() => {
        this.cancelMsg()
      });
    },
    // by
    getCategoryByNav(navigationTag) {
      GetCategory(navigationTag).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.category == null) {
            this.tableDataSelect = []
            return
          }
          this.tableDataSelect = response.data.data.category
          return
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    getCategory() {
      GetCategory('').then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.category == null) {
            this.tableData = []
            return
          }
          this.tableData = response.data.data.category
          return
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    postCategory() {
      var crateCateInput = this.crateCateInput
      var navInputValue  = this.navInputValue

      if ( navInputValue == '' ) { this.errMsg('',''); return }
      if ( crateCateInput == '' ) { this.errMsg('',''); return }

      PostCategory(crateCateInput, navInputValue).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.crateCateInput = ''
          this.successMsg()
          this.getCategory('')
          this.getCategoryByNav(this.navInputValue)
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // tag //
    GetSelCate: function(item) {
      this.cateInputValue = item.name   // ,  itme 
      this.$emit('GetSelCate', item);  // 
    },
    // 
    successMsg() {
      this.$message({
        type: 'success',
        message: ''
      });
    },
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
    cancelMsg() {
      this.$message({
        type: 'info',
        message: ''
      });
    }
  },
}
</script>

<style scoped>
.container {
  width: 100%;
}
.category {
  display: flex;
  margin-bottom: 20px;
}
.cate-edit-but {
  /* div display: flex; align-items: center;*/
  display: flex;
  align-items: center;
  margin-left: 5px
}
.cate-edit-page {
  width: 400px;
  position: fixed;
  top: 116px;
  left: 600px;
  z-index: 10;
  box-shadow: 0px 0px 26px #888888;
  background: white
}

.get-nav-input {
  margin: 10px 0 10px 0;
  margin-left: 10px;
  width: 100%
}
.create-category {
  display: flex;
  width: 100%;
  background: white;
}
.create-input {
  margin: 10px 0 10px 0;
  margin-left: 10px;
  width: 60%
}
.create-but {
  margin: 10px 10px 10px 0;
  margin-left: auto
}
</style>
