<template>
  <div class="container">
    <div style="">
      <!--  -->
      <div class="category">
        <div>
          <el-select v-model="navInputValue" @visible-change="clearCate" @change="GetSelCate" placeholder="">
            <el-option
              v-for="item in tableData"
              :key="item.tag"
              :label="item.name"
              :value="item">
            </el-option>
          </el-select>
        </div>
        <div class="cate-edit-but">
          <el-row>
            <el-button @click="cateEditBut" type="primary" plain>{{editButContent}}</el-button>
          </el-row>
        </div>
      </div>
      <!--  -->
      <div v-if="cateEditIsOpen" class="cate-edit-page">
        <!--  -->
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          style="width: 100%"
          ref="singleTable">
          <el-table-column
            label="Name"
            prop="name">
          </el-table-column>
          <el-table-column
            align="right">  
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
              <el-popconfirm 
              title="" @onConfirm="delNavigation">
                <el-button style="margin-left: 10px;" @click="handleDelete(scope.$index, scope.row)" size="mini" type="danger" slot="reference">Delete</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <div class="create-category">
          <div class="create-input">
            <el-input
              placeholder=""
              v-model="crateCateInput"
              size="small"
              clearable>
            </el-input>
          </div>
          <div class="create-but">
            <el-button @click="postNavigation" size="small" type="primary" plain></el-button>
            <!-- <el-button type="text" @click="open"></el-button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { PostNavigation } from '@/api/api'
import { DelNavigation } from '@/api/api'
import { GetNavigation } from '@/api/api'
import util from '../../../utils/utils'

export default {
  data() {
    return {
      navInputValue: '', // 
      editButContent: '',
      crateCateInput: '',
      delCateContent: '', // 
      cateEditIsOpen: false,
      tableData: [], // 
      search: '', // 
    }
  },
  mounted () {
    // , Editor.vue
    util.$on('setNavInputValue', (item) => {
      this.navInputValue = item.navigation_name
    })

    // 
    this.getNavigation()

  },
  methods: {
    // , 
    handleEdit(index, row) {
      console.log(index, row);
      //this.open2(row.name);
    },
    // , 
    handleDelete(index, row) {
      this.delCateContent = row
      //console.log(index, row);
    },
    delNavigation(){
      DelNavigation(this.delCateContent.id).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.successMsg()
          this.getNavigation()
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    cateEditBut() {
      if (this.cateEditIsOpen == true) {
        this.editButContent = ''
        this.cateEditIsOpen = false
        return
      }
      if (this.cateEditIsOpen == false) {
        this.editButContent = ''
        this.cateEditIsOpen = true
        return
      }
    },
    // 
    open2(inputValue) {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputValue: inputValue
      }).then(({ value }) => {
        if ( value == '' ) { this.errMsg('','') } 
        else { console.log(value) }
      }).catch(() => {
        this.cancelMsg()
      });
    },
    // 
    getNavigation() {
      GetNavigation().then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.navigation == null) {
            this.tableData = []
            return
          }
          this.tableData = response.data.data.navigation
          return
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 
    postNavigation() {
      var v = this.crateCateInput
      if ( v == '' ) { this.errMsg('',''); return }

      PostNavigation(v).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.crateCateInput = ''
          this.successMsg()
          this.getNavigation()
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // tag
    GetSelCate: function(item) {
      this.navInputValue = item.name // ,  itme 
      util.$emit('getCateByNav', item.tag); // Category.vue, 
      this.$emit('GetSelNav', item); // 
    },
    clearCate(e) {
      if (e == true) {
        util.$emit('clearCateByNav'); // 
        this.navInputValue = ''
      }
    },
    // 
    successMsg() {
      this.$message({
        type: 'success',
        message: ''
      });
    },
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
    cancelMsg() {
      this.$message({
        type: 'info',
        message: ''
      });
    }
  },
}
</script>

<style scoped>
.container {
  width: 100%;
}
.category {
  display: flex;
  margin-bottom: 20px;
}
.cate-edit-but {
  /* div display: flex; align-items: center;*/
  display: flex;
  align-items: center;
  margin-left: 5px
}
.cate-edit-page {
  width: 400px;
  position: fixed;
  top: 116px;
  left: 249px;
  z-index: 10;
  box-shadow: 0px 0px 26px #888888;
  background: white
}
.create-category {
  display: flex;
  width: 100%;
  background: white;
}
.create-input {
  margin: 10px 0 10px 0;
  margin-left: 10px;
  width: 60%
}
.create-but {
  margin: 10px 10px 10px 0;
  margin-left: auto
}
</style>
